import { Component, OnInit } from '@angular/core';
import {MatIconRegistry} from '@angular/material/icon';
import {DomSanitizer} from '@angular/platform-browser';
import { FrontendserviceService } from '../frontendservice.service';


@Component({
  selector: 'app-loggednavbar',
  templateUrl: './loggednavbar.component.html',
  styleUrls: ['./loggednavbar.component.css']
})
export class LoggednavbarComponent implements OnInit {

   private user=[];
   constructor(iconRegistry: MatIconRegistry, sanitizer: DomSanitizer,private service:FrontendserviceService) {
    iconRegistry.addSvgIcon(
        'thumbs-up',
        sanitizer.bypassSecurityTrustResourceUrl('assets/img/examples/thumbup-icon.svg'));
  }

  ngOnInit() {
    var username=localStorage.getItem('username');
    console.log(username,"checkcheck")
    this.service.getUserDetails(username).subscribe((data)=>{console.log(data,"hellooo")});
    this.service.getUserDetails("manoj1").subscribe((data)=>{console.log(data,"hellooo")});
  }



  logout()
  {
    this.service.logout();
  }

}